.field-text {
	margin-bottom: 16px;

	border-radius: 24px;

	&__input {
		display: block;
		width: 100%;
		padding: 16px;

		font: inherit;
		color: $color-main-light;

		border: none;
		background-color: $white;
		border-radius: 24px;

		&::placeholder {
			color: $gray;
		}

		&--hint {
			margin-bottom: 8px;
		}
	}

	&__hint {
		display: block;
		padding-left: 16px;

		font-size: $font-size-s;
		line-height: 1.715em;
		color: $gray-lighter;
	}
}
