.slider:after {
	content: 'flickity';
	display: none;

	@media screen and (min-width: 768px) {
		content: '';
	}
}

.flickity-viewport {
	overflow: initial;
}

.flickity-button-icon {
	display: none;
}

.flickity-button {
	width: 72px;
	height: 72px;

	background-color: $color-red;

	&:disabled {
		opacity: 0;
	}
}

.flickity-prev-next-button {
	transform: none;
	top: 38px;

	&:hover {
		background-color: $color-red;
	}

	&::before,
	&::after {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);

		display: block;
	}
}

.flickity-prev-next-button.next {
	&::before {
		left: 6px;

		content: 'ИЛИ';

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 800;
		text-transform: uppercase;
		color: $white;
	}

	&::after {
		right: 6px;
		content: '';

		width: 12px;
		height: 24px;

		background-image: url("../img/mobile/icon-arrow.svg");
		background-repeat: no-repeat;
		background-position: center;
	}
}

.flickity-prev-next-button.previous {
	&::before {
		left: 6px;
		content: '';

		width: 12px;
		height: 24px;

		background-image: url("../img/mobile/icon-arrow-left.svg");
		background-repeat: no-repeat;
		background-position: center;
	}

	&::after {
		right: 6px;

		content: 'ИЛИ';

		font-size: $font-size + 2;
		line-height: 1.5em;
		font-weight: 800;
		text-transform: uppercase;
		color: $white;
	}
}
