.form {
	padding: 32px 16px;
	background: linear-gradient(180deg, #FBFBFB 0%, #F1F1F1 100%);
	box-shadow: 0px -2.31384px 3.07165px rgba(0, 0, 0, 0.0196802),
							0px 0.547606px 7.38161px rgba(0, 0, 0, 0.0282725),
							0px 8.71251px 13.8989px rgba(0, 0, 0, 0.035),
							0px 23.1922px 24.7933px rgba(0, 0, 0, 0.0417275),
							0px 47.1993px 46.3731px rgba(0, 0, 0, 0.0503198),
							0px 100px 111px rgba(0, 0, 0, 0.07);
	border-radius: 24px;

	@media (min-width: $screen-md) {
		max-width: 680px;
		width: 100%;
		margin: 0 auto;
		padding: 32px 140px;

		border-radius: 48px;

		&--deliver,
		&--send {
			position: relative;

			&::before,
			&::after {
				position: absolute;

				display: block;
				content: '';
			}
		}

		&--deliver{
			&::before {
				left: -336px;
				bottom: 0;
				z-index: 1;

				width: 384px;
				height: 196px;

				background-image: url("../img/desktop/form1-left.png");
				background-repeat: no-repeat;
				background-position: center;
			}

			&::after {
				top: 0;
				right: -273px;
				z-index: 1;

				width: 245px;
				height: 845px;

				background-image: url("../img/desktop/form1-right.png");
				background-repeat: no-repeat;
				background-position: center;
			}
		}

		&--send {
			&::before {
				left: -257px;
				bottom: 90px;
				z-index: 1;

				width: 343px;
				height: 254px;

				background-image: url("../img/desktop/form2-left.png");
				background-repeat: no-repeat;
				background-position: center;
			}

			&::after {
				bottom: 78px;
				right: -392px;
				z-index: 1;

				width: 506px;
				height: 326px;

				background-image: url("../img/desktop/form2-right.png");
				background-repeat: no-repeat;
				background-position: center;
			}
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 32px;

		font-size: $font-size-l;
		line-height: 1.33333em;
		font-weight: 800;
		color: $color-secondary;

		@media (min-width: $screen-md) {
			margin-bottom: 24px;

			font-size: $font-size-xl;
			line-height: 1.25em;
		}
	}

	&__button {
		margin-bottom: 40px;

		@media (min-width: $screen-md) {
			margin-bottom: 36px;
		}
	}

	&__offer {
		max-width: 288px;
		margin: 0;

		color: $gray;
		text-align: center;

		@media (min-width: $screen-md) {
			margin: 0 auto;
		}
	}

	&__link {
		color: $color-secondary-light;
	}
}
