.button {
	width: 100%;
	padding: 8px 16px;

	font: inherit;
	font-size: 18px;
	font-weight: 800;
	color: $white;
	text-align: center;
	word-break: break-word;

	border: none;
	background: linear-gradient(180deg, #45D7D9 0%, #3D9BD3 100%);
	box-shadow: 0 18px 24px rgba(74, 189, 223, 0.8);
	border-radius: 16px;

	cursor: pointer;
	transition: all 0.3s ease-in-out;

	@media (min-width: $screen-md) {
		padding: 16px;

		font-size: $font-size-l;
		line-height: 1.67em;
	}

	&--orange {
		background: linear-gradient(180deg, #E6CF7E 0%, #DE4D2C 100%);
		box-shadow: 0 12px 24px rgba(224, 122, 72, 0.8);
	}

	&:hover,
	&:focus,
	&:active {
		transform: translateY(-8px);
	}
}
