.page-header {
	max-width: 360px;
	width: 100%;
	margin: 0 auto;
	padding: 16px 8px;
	padding-bottom: 56px;

	overflow: hidden;

	background-image: url("../img/mobile/header-bg-mobile@1x.jpg");
	background-position: center top;
	background-repeat: no-repeat;

	@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
		background-image: url("../img/mobile/header-bg-mobile@2x.jpg");
		background-size: 360px auto;
	}

	&--form,
	&--contacts {
		margin-bottom: 32px;
	}

	@media (min-width: $screen-md) {
		max-width: 1600px;
		margin-bottom: auto;

		overflow: initial;

		background-image: url("../img/desktop/header-bg-desktop@1x.jpg");

		@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
			background-image: url("../img/desktop/header-bg-desktop@2x.jpg");
			background-size: 1600px auto;
		}

		&--form,
		&--contacts {
			margin-bottom: 96px;
		}
	}

	&__logo {
		margin-bottom: 96px;
	}
}
