html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	position: relative;

	min-width: 320px;
	margin: 0;
	padding: 0;

	color: $text-color;
	font-family: $font-family;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: 500;

	overflow-x: hidden;
}

img {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
}
