.slider {
	margin: 0;
	padding: 0;

	list-style-type: none;

	@media (min-width: $screen-md) {
		display: flex;
		align-items: center;
		max-width: 1056px;
		width: 100%;
		margin: 0 auto;

		border-radius: 48px;
		box-shadow: 0px -2.31384px 3.07165px rgba(0, 0, 0, 0.0196802),
								0px 0.547606px 7.38161px rgba(0, 0, 0, 0.0282725),
								0px 8.71251px 13.8989px rgba(0, 0, 0, 0.035),
								0px 23.1922px 24.7933px rgba(0, 0, 0, 0.0417275),
								0px 47.1993px 46.3731px rgba(0, 0, 0, 0.0503198),
								0px 100px 111px rgba(0, 0, 0, 0.07);
	}

	&__item {
		position: relative;

		flex-shrink: 0;
		max-width: 288px;
		width: 100%;
		margin: 0 8px;
		padding: 16px;
		padding-top: 128px;

		background: linear-gradient(180deg, #FBFBFB 0%, #F1F1F1 100%);
		border-radius: 32px;

		@media (min-width: $screen-md) and (max-width: $screen-md + 6){
			height: 640px;
		}

		@media (min-width: 840px) and (max-width: 1068px){
			height: 640px;
		}

		@media (min-width: $screen-md) {
			display: flex;
			flex-direction: column;
			max-width: none;
			width: 50%;
			min-height: 556px;
			margin: 0;
			padding: 56px 40px;
			padding-top: 0;

			border-radius: 0;

			transition: all 0.3s ease-in-out;

			&:hover {
				background: linear-gradient(180deg, #F7F7F7 0%, #DCDCDC 100%);
			}

			&:first-child {
				border-top-left-radius: 48px;
				border-bottom-left-radius: 48px;
			}

			&:last-child {
				border-top-right-radius: 48px;
				border-bottom-right-radius: 48px;
			}
		}
	}

	&__img {
		position: absolute;
		top: -96px;
		left: 50%;
		transform: translateX(-50%);

		display: block;
		width: 224px;
		height: 224px;

		@media (min-width: $screen-md) {
			position: relative;
			top: 0;
			left: 0;
			transform: translateY(-60px);

			width: 320px;
			height: 320px;
			margin: 0 auto;
		}
	}

	&__title {
		margin: 0;
		margin-bottom: 8px;

		font-size: $font-size-l;
		line-height: 1.33333em;
		font-weight: 800;
		color: $text-color-secondary;

		@media (min-width: $screen-md) {
			margin-top: auto;
			margin-bottom: 16px;

			font-size: $font-size-l + 3;
			line-height: 1.4815em;
		}

		&--short {
			max-width: 160px;

			@media (min-width: $screen-md) {
				max-width: none;
			}
		}
	}

	&__text {
		margin: 0;
		margin-bottom: 16px;
		padding-right: 10px;

		@media (min-width: $screen-md) {
			min-height: 64px;
			margin-bottom: 32px;
			padding: 0;

			font-size: $font-size-m + 2;
			line-height: 1.6em;

			&--short {
				max-width: 300px;
			}
		}
	}

	&__button {
		@media (min-width: $screen-md) {
			&:hover,
			&:focus,
			&:active {
				transform: translateY(-16px);
			}
		}
	}
}
